import React, { useState, Suspense, useEffect } from 'react'
import keyBy from 'lodash.keyby'
import dayjs from 'dayjs'
import 'dayjs/locale/zh-cn'
import relativeTime from 'dayjs/plugin/relativeTime'

import all from './data/overall'
import provinces from './data/area'

import Tag from './Tag'

import './App.css'
import axios from 'axios'

dayjs.extend(relativeTime)

const Map = React.lazy(() => import('./Map'))

const provincesByName = keyBy(provinces, 'name')
const provincesByPinyin = keyBy(provinces, 'pinyin')

const fetcher = (url) => axios(url).then(data => {
  return data.data.data
})

function New ({ title, summary, sourceUrl, pubDate, pubDateStr }) {
  return (
    <div className="new">
      <div className="new-date">
        <div className="relative">
          {dayjs(pubDate).locale('zh-cn').fromNow()}
        </div>
        {dayjs(pubDate).format('YYYY-MM-DD HH:mm')}
      </div>
      <a className="title" href={sourceUrl}>{ title }</a>
      <div className="summary">{ summary.slice(0, 100) }...</div>
    </div>
  )
}

function News ({ province }) {
  const [len, setLen] = useState(8)
  const [news, setNews] = useState([])

  useEffect(() => {
    fetcher(`https://file1.dxycdn.com/2020/0130/492/3393874921745912795-115.json?t=${46341925 + Math.random()}`).then(news => {
      setNews(news)
    })
  }, [])

  return (
    <div className="card">
      <h2></h2>
      {
        news
          .filter(n => province ? province.provinceShortName === (n.provinceName && n.provinceName.slice(0, 2)) : true)
          .slice(0, len)
          .map(n => <New {...n} key={n.id} />)
      }
      <div className="more" onClick={() => { setLen() }}></div>
    </div>
  )
}

function Summary () {
  return (
    <div className="card info">
      <h2></h2>
      <li>
        <a href="https://m.yangshipin.cn/static/2020/c0126.html">24 | </a>
      </li>
      <li><a href="http://2019ncov.nosugartech.com/"></a></li>
      <li><a href="https://news.qq.com/zt2020/page/feiyan.htm"></a></li>
      <li><a href="https://3g.dxy.cn/newh5/view/pneumonia"></a></li>
      <li><a href="https://vp.fact.qq.com/home"></a></li>
      <li><a href="https://promo.guahao.com/topic/pneumonia"></a></li>
    </div>
  )
}

function Stat ({ modifyTime, confirmedCount, suspectedCount, deadCount, curedCount, name, quanguoTrendChart, hbFeiHbTrendChart }) {
  return (
    <div className="card">
      <h2>
         {name ? ` ${name}` : false}
        <span className="due">
          : {dayjs(modifyTime).format('YYYY-MM-DD HH:mm')}
        </span>
      </h2>
      <div className="row">
        <Tag number={confirmedCount}>
          
        </Tag>
        <Tag number={suspectedCount || '-'}>
          
        </Tag>
        <Tag number={deadCount}>
          
        </Tag>
        <Tag number={curedCount}>
          
        </Tag>
      </div>
      <div>
        <img width="100%" src={quanguoTrendChart[0].imgUrl} alt="" />
      </div>
      <div>
        <img width="100%" src={hbFeiHbTrendChart[0].imgUrl} alt="" />
      </div>
    </div>
  )
}

function Fallback () {
  return (
    <div className="fallback">
      <div>
        : <a href="https://github.com/shfshanyue/2019-ncov">shfshanyue/2019-ncov</a>
      </div>
    </div>
  )
}

function Area ({ area, onChange }) {
  const renderArea = () => {
    return area.map(x => (
      <div className="province" key={x.name || x.cityName} onClick={() => {
        // 
        if (x.name) {
          onChange(x)
        }
      }}>
        <div className={`area ${x.name ? 'active' : ''}`}>
          { x.name || x.cityName }
        </div>
        <div className="confirmed">{ x.confirmedCount }</div>
        <div className="death">{ x.deadCount }</div>
        <div className="cured">{ x.curedCount }</div>
      </div>
    ))
  }

  return (
    <>
      <div className="province header">
        <div className="area"></div>
        <div className="confirmed"></div>
        <div className="death"></div>
        <div className="cured"></div>
      </div>
      { renderArea() }
    </>
  )
}

function Header ({ province }) {
  return (
    <header>
      <div className="bg"></div>
      <h1>
        <small></small>
        <br />
          { province ? province.name : '' }
      </h1>
      <i>By </i>
    </header>
  )
}

function App () {
  const [province, _setProvince] = useState(null)
  const setProvinceByUrl = () => {
    const p = window.location.pathname.slice(1)
    _setProvince(p ? provincesByPinyin[p] : null)
  }

  useEffect(() => {
    setProvinceByUrl()
    window.addEventListener('popstate', setProvinceByUrl)
    return () => {
      window.removeEventListener('popstate', setProvinceByUrl)
    }
  }, [])

  useEffect(() => {
    if (province) {
      window.document.title = ` | ${province.name}`
    }
  }, [province])

  const setProvince = (p) => {
    _setProvince(p)
    window.history.pushState(null, null, p ? p.pinyin : '/')
  }

  const data = !province ? provinces.map(p => ({
    name: p.provinceShortName,
    value: p.confirmedCount
  })) : provincesByName[province.name].cities.map(city => ({
    name: city.fullCityName,
    value: city.confirmedCount
  }))

  const area = province ? provincesByName[province.name].cities : provinces
  const overall = province ? province : all

  return (
    <div>
      <Header province={province} />
      <Stat { ...{ ...all, ...overall } } name={province && province.name} />
      <div className="card">
        <h2> { province ? ` ${province.name}` : false }
        {
          province ? <small
            onClick={() => setProvince(null)}
          ></small> : null
        }
        </h2>
        <Suspense fallback={<div className="loading">...</div>}>
          <Map province={province} data={data} onClick={name => {
            const p = provincesByName[name]
            if (p) {
              setProvince(p)
            }
          }} />
          {
            province ? false :
              <div className="tip">
                
              </div>
          }
        </Suspense>
        <Area area={area} onChange={setProvince} />
      </div>
      <News province={province} />
      <Summary />
      <Fallback />
    </div>
  );
}

export default App;
